import React, { useState, useEffect } from 'react'
import axios from 'axios';
import './WorkerProfile.css'
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import ResultData from './ResultData';
import Uploaded from './Uploaded';
import LoadingSpinner from './LoadingSpinner';
import UploadSuccessful from './UploadSuccessful';

const WorkerProfile = () => {
  const [webmis, setWebmis] = useState([])
  const [inaData, setInaData] = useState([])
  const [resData, setResData] = useState([])
  const [uploaded, setUploaded] = useState([])  
  const [isLoading, setIsLoading] = useState(false)
  const [showModal, setShowModal] = useState(false)

  

  //extracting worker profile from WEBMIS DB
  const getData = () => {
    const url = "http://localhost:4316/api/V_HANGER_LINE_WORKER";
    axios.get(url).then(res => {
        setWebmis(res.data);
    }).catch(err => {
        console.log(err)
    })
}
useEffect(() => {
    getData();
}, [])

//extracting worker profile from INA DB
const getInaData = () => {
  const url = 'http://localhost:4316/api/InaWorkerProfile'
  axios.get(url).then(res => {
    setInaData(res.data)
  }).catch(error => {
    console.log(error)
  })
}

  useEffect(() => {
      getInaData();
    }, [])


  useEffect(() => {
    const tempData = webmis.filter(element => {
      return !inaData.some(item => item.eM_Key === element.enrolL_NO)

    });
    setResData(tempData)

  }, [webmis, inaData])

  //upload worker profile to ina database
  const updateProfile = async(e) => {
    e.preventDefault();
    
    const url = 'http://localhost:4316/api/InaWorkerProfile'
    const options = {
      headers: {
        'Content-Type': 'application/json',
      }
    };

    var i = 0;
    resData.forEach(async function (item) {
      setTimeout(async function () {

        const data = {
          eM_Key: item.enrolL_NO,
          eM_FirstName: item.employeE_NO,
          eM_LastName: " - " + item.employeE_NAME,
          eM_LCD_Name: item.employeE_NO,
          eM_ID: item.employeE_NO
        }

        const emID = await checkDuplicate(item.enrolL_NO)     
        
        //check for duplicate 
        if (item.enrolL_NO === emID ) {
          console.log("duplicate : " + emID)
          return
        }
        
        setUploaded(prevState => {
          const newState = [...prevState, data]; // update state as you did before.       

          return newState;
      });

       axios.post(url, data, options)
          .then((res) => {
            // console.log("RESPONSE ==== : ", res);            

          })
          .catch((err) => {
            console.log("ERROR: ====", err);
          }).finally(()=>{
            const dd = resData.indexOf(item)
           
            const aa = resData.splice(dd,1)
            setResData([...resData])
            console.log(resData.length)
            if(resData.length <= 0){

              setIsLoading(false)
              setShowModal(true)
            }
            
            
          })

        
      }, 100 * ++i)
    })
    
    //disable upload button
    if (resData.length <= 0) {
      setIsLoading(false)
    }
    else {
      setIsLoading(true)
    }
    
  }

  const checkDuplicate = async (id) => {
    var val = 0;
    const url = `http://localhost:4316/api/InaWorkerProfile/${id}`
    await axios.get(url).then(res=>{
      val = res.data.EM_Key
      
    }).catch(error=>{
      // console.log(error)
    })
      return val
  }

  return (
    <Container className='my-2' fluid >      
      <Row >
        <UploadSuccessful showModal={showModal} setShowModal={setShowModal} />
        <Col>        
          <h4 className='text-center'>Upload to INA system</h4>
          <ResultData resData={resData} datasync={updateProfile} isLoading={isLoading} />       
        
        </Col>
          {isLoading ? 
          <Col>
          <div className='loading'>
          {
            isLoading ? <LoadingSpinner /> : ''
          } 
          </div>
          <Uploaded uploaded={uploaded} /> 
        </Col> 
        : ""}
      </Row>
      <Row className='mt-3'>
        <Col>

          {/* <Button  >Check for Duplicate</Button> */}

        </Col>

        <Col>

        </Col>
      </Row>
    </Container>
  )
}

export default WorkerProfile


